import $config from '@client/config'
import {cloneDeep, merge} from 'lodash'
import {createUUID, deepClone} from '../../../common/utils'


// 
let elementConfig = {
  elName: '', // 
  animation: [], // 
  commonStyle: {
    position: 'absolute',
    width: $config.canvasH5Width,
    height: 30,
    top: 200,
    left: 0,
    rotate: 0,
    paddingTop: 0,
    paddingLeft: 0,
    paddingRight: 0,
    paddingBottom: 0,
    marginTop: 0,
    marginLeft: 0,
    marginRight: 0,
    marginBottom: 0,
    borderWidth: 0,
    borderColor: '#000000',
    borderStyle: 'solid',
    borderRadius: 0,
    boxShadow: '',
    fontSize: 16,
    fontWeight: 500,
    lineHeight: 1.4,
    letterSpacing: 0,
    textAlign: 'center',
    color: '#000000',
    backgroundColor: '',
    backgroundImage: '',
    backgroundSize: 'cover',
    opacity: 1,
    zIndex: 1
  }, // 
  events: [], // 
  propsValue: {}, // 
  value: '', // 
  valueType: 'String' // 
}

// 
let pageConfig = {
  name: '',
  elements: [],
  commonStyle: {
    backgroundColor: '',
    backgroundImage: '',
    backgroundSize: 'cover'
  },
  config: {}
}

// 
let projectConfig = {
  name: '',
  title: '',
  description: '',
  coverImage: '',
  author: '',
  script: '',
  width: $config.canvasH5Width,
  height: $config.canvasH5Height,
  pages: []
}

/**
 * 
 * @param {} element
 * @param {} extendStyle 
 */
let getElementConfig = (element, extendStyle = {}) => {
  let elementData = cloneDeep(element)
  let type = elementData.type || 'String' // String
  let dict = {
    'String': '',
    'Array': [],
    'Object': {},
    'Boolean': false,
    'Number': 0
  }
  let elementConfigData = cloneDeep(elementConfig)
  let config = {
    uuid: createUUID(),
    ...elementConfigData,
    elName: elementData.elName,
    propsValue: deepClone(elementData.needProps || {})
  }
  // 
  config.commonStyle = merge(config.commonStyle, elementData.defaultStyle)
  config.commonStyle = merge(config.commonStyle, extendStyle)

  config.value = element.defaultValue || dict[type]
  config.valueType = type
  config.isForm = !!Element.isForm // 
  return config
}

/**
 * 
 */
let getPageConfig = () => {
  return {
    uuid: createUUID(),
    ...cloneDeep(pageConfig)
  }
}

/**
 * 
 */
let getProjectConfig = () => {
  let project = cloneDeep(projectConfig)
  let onePage = getPageConfig()
  project.pages.push({
    ...onePage
  })
  return { ...project }
}

/**
 * 
 * @param {Object} styleObj 
 * @param {Number} scalingRatio  
 */
let getCommonStyle = (styleObj, scalingRatio = 1) => {
  // 
  let needUnitAttr = ['width', 'height', 'top', 'left', 'paddingTop', 'paddingLeft', 'paddingRight', 'paddingBottom', 'marginTop', 'marginLeft', 'marginRight', 'marginBottom', 'borderWidth', 'fontSize', 'borderRadius', 'letterSpacing']
  let style = {}
  for (let key in styleObj) {
    if (needUnitAttr.includes(key)) {
      style[key] = (styleObj[key] * scalingRatio) + 'px'
    } else {
      style[key] = styleObj[key]
    }
  }
  style.transform = `rotate(${style.rotate}deg)`
  style.backgroundImage = style.backgroundImage ? `url(${style.backgroundImage})` : ''
  return style
}

let copyElement = (element, extendStyle = {}) => {
  element = cloneDeep(element)
  element.uuid = createUUID()
  element.commonStyle = merge(element.commonStyle, extendStyle)

  // 
  element.commonStyle.top += 10
  element.commonStyle.left += 10
  return element
}

export default {
  elementConfig,
  pageConfig,
  getElementConfig,
  getProjectConfig,
  getCommonStyle,
  copyElement
}