<template>
  <div class="h5-text">
    {{text}}
  </div>
</template>
<script>
export default {
  name: 'HText',
  props: {
    text: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      defaultStyle: {
        height: 40
      }
    }
  }
}
</script>