import Template from './src/index.vue'

const install = function (Vue) {
	Vue.component(Template.name, Template)
}

//  Vue  Vue 
if (typeof window !== 'undefined' && window.Vue) {
	install(window.Vue)
}

export default {
	install,
	//  install Vue.use()
	Template
}
